* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  font-family: "Montserrat", sans-serif;
  height: 100vh;
}

/* NAVBAR */

.nav-bar {
  position: fixed;
  width: 100%;
  z-index: 3;
  background-color: #ffffff0a;
  top: 0;
  left: 0;
  transition: background-color 1s ease-in-out;
}

.nav-bar-scrolled {
  position: fixed;
  width: 100%;
  display: block;
  z-index: 3;
  background-color: #000000;
  transition: background-color 1s ease-in;
}

.nav-bar-links {
  margin: 0 auto;
  background-color: #ffffff0a;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  transition: background-color 0.5s ease-in;
  align-items: center;
}

.nav-bar_logo img {
  margin: 1rem 1rem;
  width: 250px;
  height: auto;
}

.nav-bar-link-list {
  margin: 0 1rem;
}

.links {
  color: white;
  text-decoration: none;
  margin: 0 2rem;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 0.9rem;
  outline: none;
}

.links:hover {
  color: red;
  transition: all 0.3s linear;
}

.nav-mobile {
  color: white;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  vertical-align: middle;
  transition: background-color 0.5s ease-in;
}

.nav-mobile-logo img {
  width: 150px;
  height: auto;
}

.nav-mobile-toggle {
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.toggle-menu {
  display: none;
  font-size: 2rem;
  color: white;
  z-index: 2;
}

.toggle-menu-links-list {
  width: 100%;
  height: 250px;
  transition: all 1s ease;
  position: fixed;
  z-index: 2;
}

.toggle-menu-links {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #000000;
  align-items: center;
  height: 100%;
  top: 0;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 65px;
}

.nav-mobile-toggle:checked ~ .toggle-menu-links {
  position: fixed;
  top: 80%;
}

.toggle-menu-links a {
  margin: 0.7rem 0;
}

@media (max-width: 930px) {
  .links {
    margin: 0 1rem;
  }
}

.nav-bar-other-pages {
  background: black;
}

/* HOME PAGE */

.home-page {
  background: url(./images/background-home-img.png);
  height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: relative;
}

@keyframes breath {
  0% {
    background-size: 100% auto;
  }
  50% {
    background-size: 120% auto;
  }
  100% {
    background-size: 100% auto;
  }
}

.home-text {
  color: #ffffff;
  margin: 1rem 1rem;
  text-align: left;
  height: 30vh;
  width: 80%;
}

.home-text :nth-child(1) {
  margin: 0 1rem;
  font-size: 4rem;
  letter-spacing: 5px;
  text-transform: uppercase;
  font-weight: bold;
}

.home-text :nth-child(2) {
  font-size: 1.2rem;
  margin: 1rem 1rem;
}

.home-text :nth-child(3) {
  font-size: 1rem;
  display: inline-block;
  margin: 1rem 1rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #ffffff;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

@media (max-width: 700px) {
  .home-text {
    width: 90%;
  }
}

@media (max-width: 600px) {
  .home-text :nth-child(1) {
    font-size: 2rem;
  }

  .home-text :nth-child(2) {
    font-size: 1rem;
  }
}

@media (max-width: 400px) {
  .home-text {
    text-align: center;
  }
  .home-text :nth-child(1) {
    font-size: 1.5rem;
  }
}
.home-text :nth-child(3):hover {
  color: red;
  border-bottom: 2px solid red;
  transition: all 0.3s linear;
}

.arrow {
  position: absolute;
  bottom: 5%;
  color: white;
  font-size: 2rem;
  animation: bounce 2s infinite;
  cursor: pointer;
}

.arrow:hover {
  color: red;
  transition: all 0.3s linear;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

/* TITLE */

.title {
  text-align: center;
  margin: 3rem;
  font-size: 2.3rem;
  text-transform: uppercase;
  letter-spacing: 15px;
  font-family: "Poppins", sans-serif;
}

.title h2 {
  margin: 0 0 1.5rem 0;
}

.title-span {
  display: inline-block;
  border-bottom: 2px solid black;
}

@media (max-width: 850px) {
  .title {
    font-size: 1.5rem;
  }
}

@media (max-width: 680px) {
  .title {
    font-size: 1rem;
    letter-spacing: 10px;
  }
}

@media (max-width: 550px) {
  .title {
    font-size: 0.8rem;
    margin: 3rem 1rem;
  }
}

@media (max-width: 440px) {
  .title {
    letter-spacing: 5px;
  }
}

/* HOME MENU */

.menu-items {
  display: flex;
  width: 90%;
  margin: auto;
  flex-direction: row;
  flex-wrap: wrap;
}

.menu-list {
  position: relative;
  text-align: center;
  color: white;
  text-align: center;
  flex: 20%;
  margin: 0.5rem;
  transition: transform 0.2s;
}

.menu-list:hover {
  cursor: pointer;
  transform: scale(0.9);
  opacity: 0.9;
  transition: all 0.2s linear;
}

@media (max-width: 1000px) {
  .menu-list {
    flex: 40%;
  }
}

@media (max-width: 700px) {
  .menu-list {
    flex: 80%;
  }
}

.menu-icon {
  color: white;
}

.menu-list-item img {
  width: 100%;
  height: auto;
}

.menu-list-info {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.circle {
  width: 90px;
  height: 90px;
  background: black;
  border-radius: 50%;
  position: relative;
  text-align: center;
  margin: 0;
}
.circle:hover {
  background-color: #ffffff;
  transition: all 0.5s linear;
}

.circle:hover .menu-icon {
  color: black;
  transition: all 0.2s linear;
}

.menu-icon {
  font-size: 3.3rem;
  position: absolute;
  vertical-align: middle;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
}

.menu-name {
  position: absolute;
  bottom: 0%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.3rem;
  margin: 0;
  letter-spacing: 3px;
  color: white;
}

@media (min-width: 300px) {
  .circle {
    width: 60px;
    height: 60px;
  }

  .menu-icon {
    font-size: 2rem;
  }
}

@media (min-width: 400px) {
  .circle {
    width: 70px;
    height: 70px;
  }
  .menu-icon {
    font-size: 2.5rem;
  }

  .menu-name {
    bottom: 10%;
    font-size: 1.5rem;
  }
}

@media (min-width: 600px) {
  .circle {
    width: 80px;
    height: 80px;
  }

  .menu-icon {
    font-size: 2.8rem;
  }
}

@media (min-width: 1000px) {
  .circle {
    width: 60px;
    height: 60px;
  }

  .menu-icon {
    font-size: 2.2rem;
  }
  .menu-name {
    bottom: 0;
  }
}

@media (min-width: 1300px) {
  .circle {
    width: 70px;
    height: 70px;
  }

  .menu-icon {
    font-size: 2.8rem;
  }

  .menu-name {
    bottom: 10%;
  }
}

/* HOME PROS / IMAGES / TEXT */

.pros-one {
  background: url("./images/home-backg1.png");
  height: 50vh;
  margin-top: 4rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.pros-text-styling {
  width: 90%;
  margin: 0px auto;
  height: 100%;
  color: #ffffff;
  display: block;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.pros-text-box-styling {
  width: 50%;
