}

.pros-text-styling h5 {
  font-size: 1.5rem;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin: 1rem 1rem 1rem 0;
}

.pros-text-styling p {
  color: white;
  font-weight: bold;
  display: inline-block;
  padding-bottom: 0.5rem;
  margin: 0 1rem 1rem 0;
  border-bottom: 2px solid black;
}

.button {
  padding: 1.2rem 1.8rem;
  background-color: white;
  border: none;
  cursor: pointer;
  outline: none;
  margin: 1rem 0 0 0;
}

.button p {
  border: none;
  color: black;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  letter-spacing: 2px;
}

.button:hover {
  transform: scale(1.1);
}

.button a {
  color: black;
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.button-black {
  padding: 1.2rem 1.8rem;
  background-color: #000000;
  border: none;
  cursor: pointer;
  outline: none;
  margin: 1rem 0 0 0;
  color: #ffffff;
}

.button-black p {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  letter-spacing: 3px;
}

.button-black:hover {
  transform: scale(1.1);
}

@media (max-width: 1000px) {
  .pros-text-box-styling {
    width: 60%;
    margin: 0 0 0 auto;
  }

  .pros-text-styling h3 {
    font-size: 2rem;
  }

  .pros-text-styling h5 {
    font-size: 1.2rem;
  }
}

@media (max-width: 800px) {
  .button {
    padding: 1rem 1.5rem;
  }
}

@media (max-width: 630px) {
  .pros-text-styling h3 {
    font-size: 1.4rem;
  }

  .pros-text-styling h5 {
    font-size: 0.8rem;
  }

  .button a {
    padding: 0.6rem 1rem;
    font-size: 0.8rem;
