/*ABOUT WHY US LIST */

.about-why-us {
  background-color: #f5f5f5;
  padding: 4rem 0;
}

.about-why-us-list {
  width: 90%;
  margin: auto;
  display: flex;
  max-width: 1500px;
}

.about-why-us-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 0 1rem;
}

.about-why-us-circle {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.212);

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  color: #ffffff;
  box-shadow: -1px 1px 5px 3px rgba(0, 0, 0, 0.75);
  animation: myanimation 10s ease-in infinite;
}

@keyframes myanimation {
  0% {
    background-color: #000000;
  }
  25% {
    background-color: #ffffff;
    color: #000000;
  }
  75% {
    background-color: red;
    color: #ffffff;
  }
  100% {
    background-color: #000000;
  }
}

.about-why-us-icon {
  font-size: 3rem;
}

.about-why-us-text {
  text-align: left;
  width: 70%;
  margin: auto;
}

.about-why-us-text h5 {
  text-transform: capitalize;
  margin: 1rem;
  font-size: 1.2rem;
  animation: textchanging 5s ease-in infinite;
}

@keyframes textchanging {
  0% {
    color: #000000;
  }
  50% {
    color: red;
  }
  100% {
    color: #000000;
  }
}

.about-why-us-text p {
  font-size: 0.9rem;
  margin: 1rem;
}

@media (max-width: 1280px) {
  .about-why-us-list {
    flex-wrap: wrap;
  }
  .about-why-us-item {
    flex-basis: 45%;
  }
}

@media (max-width: 1050px) {
  .about-why-us-text h5 {
    font-size: 1rem;
  }
}

@media (max-width: 900px) {
  .about-why-us-list {
    justify-content: center;
  }
  .about-why-us-item {
    flex-direction: column;
  }

  .about-why-us-text {
    text-align: center;
    width: 100%;
  }
}

@media (max-width: 750px) {
  .about-why-us {
    padding: 3rem 0;
  }
  .about-why-us-list {
    flex-direction: column;
  }
  .about-why-us-circle {
    width: 70px;
    height: 70px;
  }

  .about-why-us-item {
    margin: 1rem 0;
  }
}

/* ABOUT FAQ */

.faq {
  width: 60%;
  margin: 0 auto;
}

.Collapsible {
  cursor: pointer;
  width: 100%;
  margin: 30px 0;
  padding: 20px;
  background-color: #eee;
  -webkit-box-shadow: 0px 6px 30px -8px rgba(0, 0, 0, 0.53);
  -moz-box-shadow: 0px 6px 30px -8px rgba(0, 0, 0, 0.53);
  box-shadow: 0px 6px 30px -8px rgba(0, 0, 0, 0.53);
}

.Collapsible__trigger {
  font-size: 18px;
  font-weight: bold;
}

.faq-answer {
  border-radius: 20px;
  color: rgb(85, 85, 85);
  margin-top: 5px;
  padding: 20px 10px 20px 10px;
  border: 1px solid #eee;
  background-color: white;
}

@media (max-width: 750px) {
  .faq {
    width: 95%;
  }

  .Collapsible {
    margin: 20px 0;
  }
}

/* ABOUT TEAM MEMBERS */

.about-team-members-list {
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.about-team-member {
  margin: 2rem;
  text-align: center;
}

.about-team-member img {
  width: 250px;
  height: auto;
}

.team-item-description h3 {
  margin: 1.2rem 1rem 0.5rem 1rem;
}

.team-item-description p {
  color: #7e7e7e;
}

.about-team-member:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 6px -6px black;
  padding: 0.5rem;
  transition: all 0.3s ease-in-out;
}

.about-team-member:hover .social-links {
  display: block;
}

.social-links {
  margin: 0.5rem;
  font-size: 1.4rem;
  cursor: pointer;
  display: none;
}

.social-links a {
  color: #000000;
}

.social-links span {
  margin: 0 0.3rem;
}

@media (max-width: 750px) {
  .social-links {
    display: block;
  }
}

@media (max-width: 400px) {
  .about-team-member {
    margin: 1rem auto;
  }
}

/* CONTACTS */

.contacts {
  display: flex;
  width: 90%;
  margin: 2rem auto;
}

.contacts-form {
  flex: 70%;
  max-width: 900px;
  margin-left: 3rem;
}

.contacts-form-fields form {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.contacts-form-fields input {
  flex: 40%;
}

.contacts-form-fields input,
.contacts-form-fields textarea {
  border: 1px solid rgb(187, 187, 187);
  box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.06);
  padding: 1rem;
  margin: 1rem;
  outline: none;
}

.contacts-form h3 {
  margin: 1rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  display: inline-block;
  border-bottom: 1px solid #e4e4e4;
  padding-bottom: 1rem;
  font-size: 1.3rem;
}

.contacts-form-fields textarea {
  flex: 100%;
  height: 150px;
  resize: none;
  color: grey;
}

.contacts-form button {
  margin: 1rem;
}

/* CONTACTS IFNO */

.contacts-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 4rem;
  border-left: 1px solid #e4e4e4;
  flex: 30%;
}

.contacts-social {
  margin: 1rem;
}

.contacts-social a {
  font-size: 1.5rem;
  color: black;
}

.contacts-social span {
  padding: 0 0.5rem;
}

.contacts-info-data {
  margin-left: 1rem;
  border-bottom: 1px solid #e4e4e4;
}

.contacts-info-data > p {
  margin: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
}

.contacts-info-value {
  margin: 1rem;
  display: flex;
}

.contacts-info-value span {
  color: black;
  font-size: 1.3rem;
}

.contacts-info-value p {
  margin: 0 1rem;
}

@media (max-width: 1000px) {
  .contacts-info {
    margin: 0 2rem;
  }
}

@media (max-width: 900px) {
  .contacts {
    flex-direction: column;
  }

  .contacts-form {
    width: 100%;
    margin: auto;
  }

  .contacts-info {
    flex-direction: row;
    flex: 100%;
    flex-wrap: wrap;
    border: none;
    margin: 0;
  }

  .contacts-info-data {
    flex: 40%;
    border: none;
    margin: 1rem;
  }
}

@media (max-width: 720px) {
  .contacts-info-data > p {
    font-size: 1rem;
  }

  .contacts-info-value p {
    font-size: 0.9rem;
  }
}

@media (max-width: 510px) {
  .contacts-button-style {
    width: 94%;
    margin: auto;
  }

  .contacts-form button {
    margin: 1rem 0;
  }

  .button-black {
    width: 100%;
  }
}

@media (max-width: 620px) {
  .contacts-info-data {
    flex: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .contacts-info-value {
    margin: 0 1rem;
  }
}

/* FOOTER */

.footer {
  background-color: #000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  margin-top: 3rem;
}

.footer-logo img {
  width: 200px;
  height: auto;
  margin-bottom: 1.5rem;
}

.social a {
  color: white;
  font-size: 1.7rem;
}

.social {
  padding: 0 0.5rem;
}

.social span {
  margin: 0 1rem;
}
